<template>
  <div class="col-md-3">
    <b-input-group
      id="searchBar"
      v-click-outside="removeFieldFocus"
      size="lg"
      placeholder="Search"
    >
      <b-input-group-text slot="append">
        <img
          class="search-button"
          src="@/assets/search.png"
          alt
          @click="search"
        />
      </b-input-group-text>
      <b-form-input
        v-model="searchTerm"
        placeholder="Search"
        @focus="fieldFocus = true"
        @keyup.enter="search"
      ></b-form-input>
      <SearchSuggestions
        :field-focus="fieldFocus"
        :search-term="searchTerm"
        @removeFieldFocus="removeFieldFocus"
      />
    </b-input-group>
  </div>
</template>

<script>
import Store from "@/store";
import SearchSuggestions from "@/components/SearchSuggestions";

export default {
  components: {
    SearchSuggestions
  },
  data() {
    return {
      store: Store.state,
      searchTerm: "",
      universesList: null,
      fieldFocus: false
    };
  },
  methods: {
    search() {
      Store.addFilter(null, "globalSearch", this.searchTerm);
    },
    removeFieldFocus() {
      this.fieldFocus = false;
    }
  }
};
</script>

