<template>
  <div v-if="suggestions.length && fieldFocus" id="search-suggestions">
    <ul>
      <li
        v-for="(suggestion, index) in suggestions"
        :key="
          suggestion.brandName +
            suggestion.temper +
            suggestion.isoDesignation +
            index
        "
        @click="searchByKeyword(suggestion)"
      >
        {{ suggestion.brandName }}
      </li>
    </ul>
  </div>
</template>

<script>
import Store from "@/store";

export default {
  props: {
    searchTerm: {
      type: String,
      default: null
    },
    fieldFocus: {
      type: Boolean,
      default: false
    },
    removeFieldFocus: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      store: Store.state,
      suggestions: []
    };
  },
  watch: {
    searchTerm: function() {
      if (this.searchTerm.length > 2) {
        this.getSuggestions();
      } else {
        this.suggestions = [];
      }
    }
  },
  methods: {
    getSuggestions() {
      this.suggestions = this.store.alloysBackup.filter(alloy => {
        const searchTerm = this.searchTerm.toLowerCase().trim();

        // Search on alloysBrand
        const brandName = alloy["brandName"].toLowerCase();
        if (brandName.includes(searchTerm)) {
          return true;
        }

        // Search through alloys keywords
        for (let keyword of alloy["keywords"]) {
          const keywordFormatted = keyword.toLowerCase();
          if (keywordFormatted.includes(searchTerm)) {
            return true;
          }
        }
      });
    },
    searchByKeyword(alloySelected) {
      Store.searchByKeyword(alloySelected);
      this.$emit("removeFieldFocus");
    }
  }
};
</script>

