  background-color: white;
  color: black;
  margin-top: 5px;
  position: absolute;
  // top = searchBar height
  top: 40px;
  left: 0;
  z-index: 1;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.2) 2px 3px 4px;
  border-radius: 5px;

  ul {
    padding: 10px 0;
    margin-bottom: 0;
  }

  li {
    list-style: none;
    padding: 5px 20px;

    &:hover {
      cursor: pointer;
      background: rgba(0, 0, 0, 0.2);
    }
  }
}
</style>
